    return {
      dateFilterRanges,
      dateRangeSelected,
      dateFilterSelectedIndex,
      dateRangeSelectedDisplay,
      dateFilterSelected,
      showDateFilter,
      pickerDate,
      dateFilterChange,
      isApplyFilterBtnValid,
