      displayedHeaderSearch,
      status,
      routingSlipNumber,
      receiptNumber,
      dateFilter,
      folioNumber,
      initiator,
      totalAmount,
      searchRoutingSlipResult,
      applyDateFilter,
      searchNow,
      canShowColumn,
      getStatusLabel,
      addRoutingSlip,
      searchParamsExist,
      clearFilter,
      formatFolioResult,
      showExpandedFolio,
      toggleFolio,
      isLoading
